.kuma-grid-actionbar{
    width:100%;
    padding:6px 0px 16px;
}

.kuma-grid-actionbar .kuma-button{
    margin-right: 10px;
    padding: 0 30px;
    background: #fafafa;
    &:hover {
        color: #666;
    }
}
.g-fr{
    float: right;
}

a.kuma-button-swhite{
    line-height: 26px;
    height: 28px;
}

.kuma-grid-searchbar{
    float:right;
    position:relative;
}

.kuma-grid-searchbar .kuma-icon-search{
    position: absolute;
    right: 8px;
    color: #ccc;
    top: 4px;
    cursor:pointer;
}


